<template lang="html">
  <div class="">
    <default-head header="移动分组"></default-head>
    <div id="wrapper">
      <white-title @click.native="addNewGroup" borderB="1px solid #ddd" text="添加到新的分组" :isShow="true"></white-title>
      <!-- <white-title borderB="1px solid #ddd" text="全部" @click.native="saveToItem(item)"></white-title> -->
      <white-title v-for="(item, index) in groupArr" :key="item.index"  borderB="1px solid #ddd" :text="item.name" @click.native="saveToItem(item)"></white-title>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import whiteTitle from '@/components/common/whiteTitle'
export default {
  data () {
    return {
      groupArr: []
    }
  },
  components: {defaultHead, whiteTitle},
  mounted () {
    this.getGroupList()
  },
  computed: {
    searchId: function () {
      return sessionStorage.getItem('searchId')
    },
    isLogin: function () {
      return utils.isLogin()
    },
    userId: function () {
      return utils.userId()
    }
  },
  methods: {
    ajaxAddGroup (val, action) {
    },
    // 添加新分组
    addNewGroup () {
      let self = this
      minutils.MessageBoxPrompt({
        title: '提示',
        options: {
          inputPlaceholder: '分组名(1-16个字符)'
        },
        success (value, action) {
          if (!value) return
          // 保存收藏分组
          api.saveCollectionTypeList({'userId': self.userId, 'name': value, 'sortBy': 1}).then((response) => {
            self.getGroupList()
          })
        },
        fail (action) {
        }
      })
    },
    // 获取分组列表
    getGroupList () {
      api.getCollectionTypeList({'userId': this.userId}).then((response) => {
        this.groupArr = [{name: '全部', id: 0}].concat(response.body.data)
      })
    },
    saveToItem (item) {
      let self = this
      let collectId = parseInt(window.location.search.substring(4))
      // type item.id userId this.userId id id
      // 根据参数判断收藏/移动
      if (window.location.search) {
        minutils.MessageBoxConfirm({
          message: `是否移动到${item.name}？`,
          title: '提示',
          options: {
            confirmButtonText: '确认',
            cancelButtonText: '取消'
          },
          success (action) {
            self.changeCollectionById(self.userId, collectId, item.id)
          }
        })
      } else {
        self.saveCollectionById(item)
      }
    },
    saveCollectionById (item) { // 保存收藏
      let self = this
      minutils.MessageBoxConfirm({
        message: `是否添加到${item.name}？`,
        title: '提示',
        options: {
          confirmButtonText: '确认',
          cancelButtonText: '取消'
        },
        success (action) {
          api.saveCollectionById({userId: self.userId, collectId: self.searchId, type: item.id}).then((response) => {
            if (response.body.isSuccess) {
              minutils.Toast({
                message: '收藏成功',
                duration: 1000
              })
              self.$router.push('/detail')
            }
          })
        },
        fail (action) {
        }
      })
    },
    delCollectionList (delId) {
      api.delCollectionById({'userId': this.userId, 'ids': delId}).then((response) => {
      })
    },
    changeCollectionById (userId, id, type) {
      api.changeCollectionById({userId: userId, id: id, type: type}).then((response) => {
        this.$router.push('/friendcircle')
      })
    }
  }
}
</script>

<style lang="css">
#wrapper div:last-child p {
  border: none !important;
}
</style>



